<template>
  <div class="container">
    <el-row>
      <el-col :span="8" >
        <el-card shadow="hover" >
          <div v-for="(item, index) in items" :key="index">
            <img src="" alt="试卷">
            <div>
              <p>{{title}}</p>
              <span><img src="require('image/browser.png').default" alt="题库" width="200px" height="200px">共{{number}}题</span>
              <span><img src="./image/browser.png" alt="浏览">共{{watcher}}题</span>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        title: '你',
        number: 10,
        watcher: 20,
        items: ''
      }
    },
    methods: {
      async getCompanyTest() {
        const { data: res } = await this.$http.get('/api/getCompanyTest');
        console.log(res,'res');
        this.items = res;
      } 
    },
    mounted() {
      this.getCompanyTest();
    }
  }
</script>

